import grid_system from './grid_system/';

export default (editor, config = {}) => {
  const bm = editor.BlockManager;

  // blocks are the large pieces of UI we want to to use
  // we may want to have dragable blocks that are also individual components
  //  make functions that return blocks and components to get added, loops through master list first? 
  //  the master list check is just holdover from the old plugin I copied online, might not need it and would probably make it faster
  //  make a hash table to keep block names and block objects, put them all in an array, loop through array and call addBlock
  //  example: [ { id: 'name', opts: { label: ... } }, ... ]
  const grid_system_blocks = grid_system(editor, config);
  
  const blocks = [
    'address', 
    'alert', 
    'blockquote', 
    'button', 
    'container', 
    'container_fluid', 
    'column', 
    'columns-2', 
    'columns-3', 
    'columns-4', 
    'columns-4-8', 
    'columns-8-4', 
    'dropdown', 
    'header', 
    'image', 
    'label', 
    'link', 
    'list', 
    'media', 
    'panel', 
    'paragraph', 
    'row', 
    'thumbnail', 
    'well'
  ];

  const addBlock = (id = '', opts = {}) => (blocks.indexOf(id) >= 0) ? bm.add(id, opts) : null

  grid_system_blocks.forEach(block => addBlock(block.id, block.opts));

}
